.tooltip {
  position: relative;
  margin-left: var(--small-offset); }

.tooltip__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  box-shadow: 2px 4px 10px rgba(0, 13, 84, 0.2);
  background: var(--color-white); }
  .tooltip__btn svg {
    width: 6px;
    height: 10px; }

.tooltip__txt {
  position: absolute;
  left: -10px;
  bottom: calc(100% + 14px);
  z-index: 3;
  border-radius: 4px;
  padding: var(--small-offset) var(--second-offset);
  width: max-content;
  max-width: 426px;
  box-shadow: 4px 8px 20px rgba(0, 13, 84, 0.2);
  font: var(--small-book);
  text-transform: initial;
  color: var(--color-main);
  background: var(--color-white);
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out; }

.tooltip__txt::after {
  content: "";
  position: absolute;
  left: 10px;
  bottom: -8px;
  border-style: solid;
  border-width: 10px 8px 0;
  border-color: var(--color-white) transparent transparent transparent;
  border-radius: 4px;
  width: 0;
  height: 0; }

.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0); }

.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0); }

.input {
  border: none;
  border-radius: 4px;
  padding: 0 var(--second-offset);
  width: 100%;
  height: 60px;
  font: var(--middle-book);
  color: var(--color-main);
  background-color: var(--color-white); }
  .input::placeholder {
    color: var(--color-gray); }

/* Components */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }

.header__container {
  align-items: center; }

.header__left {
  grid-column: 2 span; }

.header__right {
  display: flex;
  justify-content: flex-end;
  grid-column: 10 span; }

.nav__list {
  display: flex;
  align-items: center; }

.nav__item:not(:last-child) {
  margin-right: var(--main-offset); }

.nav__link {
  font: var(--small-book);
  color: var(--color-white); }

.cart {
  margin-left: var(--main-offset); }

.cart__btn {
  position: relative;
  width: 20px;
  height: 20px; }
  .cart__btn svg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover; }

.cart__count {
  position: absolute;
  right: -6px;
  bottom: -6px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  height: 16px;
  min-width: 16px;
  font: var(--small-book);
  color: var(--color-white);
  background-color: var(--color-accent); }

.hero {
  height: 800px;
  background-color: var(--color-eastbay); }

.logo {
  font: var(--middle-high-bold);
  color: var(--color-white); }
